<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        (/)
      </div>
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-input v-model="name" placeholder=""></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input numberInput">
          <el-input-number v-model="num" controls-position="right" :min="0"></el-input-number>
        </div>
        <div class="numberText"></div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input numberInput">
          <el-input-number v-model="num" controls-position="right" :min="0"></el-input-number>
        </div>
        <div class="numberText"></div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-upload
            class="uploadFile"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="fileList">
            <el-button size="small" class="CoResetBtn" icon="el-icon-upload2"></el-button>
            <div slot="tip" class="el-upload__tip">.png .jpg...</div>
          </el-upload>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()"></el-button>
        <el-button class="CoResetBtn" @click="goPrePage()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "OnlineTime",
    data(){
      return{
        name : '',
        status : '',
        num : '',
        fileList:[],
        selectOptions: [
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }
        ],
        checkList:[]
      }
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }

</style>



