import MUtil from '@/utils/mm.js'

const task = {
  // type
  getTaskList(data){
    return MUtil.request('/mission/getMissions',data)
  },
  ///
  setMissionStatus(data){
    return MUtil.request('/mission/setMissionStatus',data)
  },
  //
  updateMission(data){
    return MUtil.formRequest('/mission/updateMission',data)
  }
};


export default task



