<template>
  <div class="centerCon">
    <div class="centerTitle">
      
    </div>
    <div class="searchCon">
      <div class="searchItem userSearchItem">
        <div class="searchLabel"></div>
        <div class="searchInput userInput">
          <el-input class="searchItem" v-model="search.tel_number" placeholder=""></el-input>
        </div>
      </div>
      <div class="searchItem userSearchItem">
        <div class="searchLabel"></div>
        <div class="searchInput userInput">
          <el-input class="searchItem" v-model="search.nickName" placeholder=""></el-input>
        </div>
      </div>
      <div class="searchItem userSearchItem">
        <div class="searchLabel"></div>
        <div class="searchInput searchSelect userSelect">
          <el-select v-model="search.hasVerifyWechat" :popper-append-to-body = false placeholder="">
            <el-option
              v-for="item in selectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="searchItem userSearchItem">
        <div class="searchLabel"></div>
        <div class="searchInput searchSelect userSelect">
          <el-select v-model="search.activity" :popper-append-to-body = false placeholder="">
            <el-option
              v-for="item in activitySelectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="searchBtnCon">
        <el-button type="primary" class="CoBtn" @click="searchList()"></el-button>
        <el-button class="CoResetBtn" @click="reset()"></el-button>
      </div>
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        {{page.total}}
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">

        <el-table-column slot="table_oper" align="center" label="" width="80" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div v-if="scope.row.userStatus.activity === 'enable'">
              <el-badge is-dot class="dots" type="success"></el-badge>
              <span></span>
            </div>
            <div v-if="scope.row.userStatus.activity === 'disable'">
              <el-badge is-dot class="dots" type="danger"></el-badge>
              <span></span>
            </div>
          </template>
        </el-table-column>

        <el-table-column slot="table_oper" align="center" label="" width="200" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div class="tableBtn">
              <el-button type="text" @click="detailPop(scope.row.uuid)"></el-button>
            </div>
            <el-divider direction="vertical"></el-divider>
            <div class="tableBtn">
              <el-popover trigger="hover" placement="top">
                <div class="confirmText">
                  <span class="el-icon-warning confirmIcon"></span>
                  <span class="confirmTips" v-if="scope.row.userStatus.activity === 'enable'"></span>
                  <span class="confirmTips" v-if="scope.row.userStatus.activity === 'disable'"></span>
                </div>
                <div class="confirmBtnCon">
                  <el-button type="primary" @click="pSubmit(scope.row.uuid,scope.row.userStatus.activity)"></el-button>
                </div>
                <div slot="reference">
                  <el-button type="text" v-if="scope.row.userStatus.activity === 'enable'"></el-button>
                  <el-button type="text" v-if="scope.row.userStatus.activity === 'disable'"></el-button>
                </div>
              </el-popover>
            </div>
          </template>
        </el-table-column>
      </commonTable>
    </div>
  </div>
</template>

<script>
  import USER from 'admin/service/user-service.js'
    export default {
      name: "UserMg",
      data() {
        return {
          search: {
            tel_number: '',
            nickName: '',
            hasVerifyWechat:'',
            activity : '',
          },
          selectOptions: [
            {
              value: 'enable',
              label: ''
            }, {
              value: 'disable',
              label: ''
            }
          ],
          activitySelectOptions:[
            {
              value: 'enable',
              label: ''
            }, {
              value: 'disable',
              label: ''
            }
          ],
          columns: [
            {prop: 'tel_number', label: '', width: '150', align: 'left'},
            {prop: 'nickName', label: '', width: '150', align: 'left'},
            {prop: 'Bcoins', label: '', align: 'right',width: '96'},
            {prop: '', label: '', align: 'right',width: '96',formatter: this.formatter},
            {prop: 'coin', label: 'OPENID', align: 'center',width: '96'},
            {prop: 'coin', label: '', align: 'left',width: '96'}
          ],
          tableData: [],
          page: {
            page: 1,
            unit: 10,
            total: 0
          },
        }
      },
      created(){
        this.getList();
      },
      methods: {
        //
        getList(){
          let data ={
            'page' : this.page.page,
            'unit': 10,
            'tel_number':this.search.tel_number,
            'nickName':this.search.nickName,
            'hasVerifyWechat':this.search.hasVerifyWechat,
            'activity':this.search.activity
          }
          USER.getUserList(data)
            .then(res => {
              console.log('res',res);
              if(res.code === "0"){
                this.tableData = res.data;
                this.page.total = res.count;
              }
            })
        },
        // 
        formatter(row, column, cellValue) {
          let wechat = row.userStatus.hasVerifyWechat;
          if(wechat === 'true'){
            return ''
          }else {
            return ''
          }
        },
        //
        searchList(){
          this.page.page = 1;
          this.getList();
        },
        //
        reset(){
          this.search.tel_number = '';
          this.search.nickName = '';
          this.search.hasVerifyWechat = '';
          this.search.activity = '';
        },
        // 
        handleCurrentChange(val) {
          this.page.page = val;
          this.getList();
        },
        //
        goAddGoods(){
          this.$router.push({
            name:'AddGoods',
          })
        },
        //
        pSubmit(uuid,status) {
          console.log('uuid',uuid);
          console.log('status',status);
          let activity = status === 'enable' ? 'disable' : 'enable';
          let data = {
            'uuid' : uuid,
            'activity' : activity
          };

          USER.setUserstatus(data)
            .then(res => {
              console.log('res',res);
              if(res.code === "0"){
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.getList();
              }
            })

        },
        //
        detailPop(uuid){
          this.$router.push({
            name:'UserInfo',
            query:{
              'uuid':uuid
            }
          })
        }
      }
    }
</script>

<style lang="css" scoped>
  .userInput{
    width: 168px;
  }
  .userSelect{
    width: 120px;
  }
  .userSearchItem{
    margin-right: 40px;
  }
</style>



