<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        
      </div>
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-input v-model="name" placeholder=""></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-checkbox-group v-model="checkList">
            <el-checkbox label=" A"></el-checkbox>
            <el-checkbox label=" B"></el-checkbox>
            <el-checkbox label=" C"></el-checkbox>
          </el-checkbox-group>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()"></el-button>
        <el-button class="CoResetBtn" @click="goPrePage()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "AddType",
    data(){
      return{
        name : '',
        status : '',
        num : '',
        selectOptions: [
          {
            value: '1',
            label: ''
          }, {
            value: '2',
            label: ''
          }
        ],
        checkList:[]
      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



