<template>
  <div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        2632243
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">
      </commonTable>
    </div>
  </div>
</template>

<script>
  import USER from 'admin/service/user-service.js'
  export default {
    name: "Account",
    data() {
      return {
        columns: [
          {prop: 'name', label: '', width: '200', align: 'left'},
          {prop: 'detail', label: '', width: '300', align: 'left'},
          {prop: 'coin', label: '', align: 'right',width: '150'},
          {prop: 'coin', label: '', align: 'right',width: '150'},
          {prop: 'coin', label: '', align: 'right',width: '150'}
        ],
        tableData: [],
        page: {
          page: 1,
          unit: 10,
          total: 0
        }
      }
    },
    created(){
      this.getList();
    },
    methods: {
      //
      getList(){
        let data ={
          'page' : this.page.page,
          'unit': 10
        };
        USER.getUserList(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.tableData = res.data;
              this.page.total = res.count;
            }
          })
      },
      // 
      handleCurrentChange(val) {

      },
      //
      goAddGoods(){
        this.$router.push({
          name:'AddGoods',
        })
      },
      //
      pSubmit(row) {

      }
    }
  }
</script>

<style lang="css" scoped>

</style>



