<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="searchCon">
        <div class="searchItem">
          <div class="searchLabel"></div>
          <div class="searchInput">
            <el-input class="searchItem" v-model="search.title" placeholder=""></el-input>
          </div>
        </div>
        <div class="searchItem">
          <div class="searchLabel"></div>
          <div class="searchInput searchSelect">
            <el-select v-model="search.status" :popper-append-to-body = false placeholder="">
              <el-option
                v-for="item in selectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="searchBtnCon">
          <el-button type="primary" class="CoBtn" @click="searchList()"></el-button>
          <el-button class="CoResetBtn" @click="reset()"></el-button>
        </div>
    </div>
    <div class="btnGroupCon">
      <el-button type="primary" class="CoBtn" icon="el-icon-plus" @click="goAddGoods()"></el-button>
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        {{page.total}}
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">
        <el-table-column slot="table_oper" align="left" label="" width="200" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div v-html="scope.row.description"></div>
          </template>
        </el-table-column>

        <el-table-column slot="table_oper" prop="price" align="right" label="" width="80" :resizable="false" fixed="right"></el-table-column>


        <el-table-column slot="table_oper" align="center" label="" width="88" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div class="tableBtn">
              <el-popover trigger="hover" placement="top">
                <div class="confirmText">
                  <span class="el-icon-warning confirmIcon"></span>
                  <span class="confirmTips" v-if="scope.row.isRecommend === false"></span>
                  <span class="confirmTips" v-else-if="scope.row.isRecommend === true"></span>
                </div>
                <div class="confirmBtnCon">
                  <el-button type="primary" @click="pSubmit(scope.row.uuid,scope.row.isRecommend,'setRecommendGood')"></el-button>
                </div>
                <div slot="reference">
                  <el-button type="text">{{scope.row.isRecommend ? '' : ''}}</el-button>
                </div>
              </el-popover>
            </div>
          </template>
        </el-table-column>
        <el-table-column slot="table_oper" prop="inventory" align="right" label="" width="80" :resizable="false" fixed="right"></el-table-column>
        <el-table-column slot="table_oper" align="center" label="" width="80" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div v-if="scope.row.status === 'disable'">
              <el-badge is-dot class="dots" type="danger"></el-badge>
              <span></span>
            </div>
            <div v-if="scope.row.status === 'enable'">
              <el-badge is-dot class="dots" type="success"></el-badge>
              <span></span>
            </div>

          </template>
        </el-table-column>

        <el-table-column slot="table_oper" align="center" label="" width="200" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div class="tableBtn" @click="editGoods(scope.row.uuid)">
              <el-button type="text"></el-button>
            </div>

            <el-divider direction="vertical"></el-divider>
            <div class="tableBtn">
              <el-popover trigger="hover" placement="top">
                <div class="confirmText">
                  <span class="el-icon-warning confirmIcon"></span>
                  <span class="confirmTips" v-if="scope.row.status === 'disable'"></span>
                  <span class="confirmTips" v-if="scope.row.status === 'enable'"></span>
                </div>
                <div class="confirmBtnCon">
                  <el-button type="primary" @click="pSubmit(scope.row.uuid,scope.row.status,'setGoodStatus')"></el-button>
                </div>
                <div slot="reference">
                  <el-button type="text" v-if="scope.row.status === 'disable'"></el-button>
                  <el-button type="text" v-if="scope.row.status === 'enable'"></el-button>
                </div>
              </el-popover>
            </div>

            <el-divider direction="vertical"></el-divider>

            <div class="tableBtn">
              <el-popover trigger="hover" placement="top">
                <div v-if="scope.row.status === 'enable'">
                  <div class="delTips"></div>
                  <div class="delArrow"></div>
                </div>
                <div v-else>
                  <div class="confirmText">
                    <span class="el-icon-warning confirmIcon"></span>
                    <span class="confirmTips" v-if="scope.row.status === 'disable'"></span>
                  </div>
                  <div class="confirmBtnCon">
                    <el-button type="primary" @click="pSubmit(scope.row.uuid,scope.row.status,'delGoods')"></el-button>
                  </div>
                </div>
                <div slot="reference">
                  <el-button type="text"></el-button>
                </div>
              </el-popover>
            </div>
          </template>
        </el-table-column>
      </commonTable>
    </div>






  </div>
</template>

<script>
  import GOODS from 'admin/service/goods-service.js'
  export default {
    name: 'GoodsMg',
    data() {
      return {
        search : {
          title : '',
          status : ''
        },
        selectOptions : [
          {
            value: 'enable',
            label: ''
          }, {
            value: 'disable',
            label: ''
          }
        ],
        columns: [
          {prop: 'title', label: '', width: '200', align: 'left'}
        ],
        tableData: [],
        page: {
          page: 1,
          unit: 10,
          total: 0
        },

      }
    },
    created(){
      this.getList();
    },
    methods: {
      //
      getList(){
        let data ={
          'page' : this.page.page,
          'unit': 10,
          'title': this.search.title,
          'status':this.search.status
        };
        GOODS.getGoodsList(data)
          .then(res => {
          console.log('res',res);
          if(res.code === "0"){
            this.tableData = res.data;
            this.page.total = res.count;
          }
        })
      },
      //
      searchList(){
        this.page.page = 1;
        this.getList();
      },
      //
      reset(){
        this.search.title = '';
        this.search.status = '';
      },
      // name
      formatter(row, column, cellValue) {
        // return row.detail | ellipsis;
      },
      // 
      handleCurrentChange(val) {
        this.page.page = val;
        this.getList();
      },
      //
      goAddGoods(){
        this.$router.push({
          name:'AddGoods',
        })
      },
      //
      pSubmit(uuid,status,type) {
        let statusNew = (status === 'disable') ? 'enable' : 'disable';
        let setData = {
          'uuid' : uuid,
          'status': statusNew
        };
        let delData = {
          'uuid' : uuid
        };
        let recommendData = {
          'uuid' : uuid,
          'status': status ? false : true
        };
        if(type === 'setGoodStatus'){
          GOODS.setGoodStatus(setData)
            .then(res => {
              console.log('res',res);
              if(res.code === "0"){
                this.getList();
              }
            })
        }else if(type === 'delGoods'){
            GOODS.delGood(delData)
              .then(res => {
                console.log('res',res);
                if(res.code === "0"){
                  this.$message({
                    type: 'success',
                    message: ''
                  });
                  this.getList();
                }
              })
        }else if(type === 'setRecommendGood'){
          GOODS.setRecommendGood(recommendData)
            .then(res => {
              console.log('res',res);
              if(res.code === "0"){
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.getList();
              }
            })
        }
      },
      //
      editGoods(uuid){
        this.$router.push({
          name:'AddGoods',
          query:{
            'uuid' : uuid
          }
        })
      }
    }
  }
</script>


<style lang="css" scoped>
  .searchInput{
    width: 256px;
    height: 32px;
    float: left;
  }
  .searchSelect{
    width: 224px;
  }

</style>



