<template>
  <div>
    <div class="centerTitle">
      
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        
      </div>
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-input v-model="data.title" placeholder="" :disabled="true"></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input numberInput">
          <el-input-number v-model="data.requireAmount" controls-position="right" :min="0"></el-input-number>
        </div>
        <div class="numberText"></div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input numberInput">
          <el-input-number v-model="data.reward" controls-position="right" :min="0"></el-input-number>
        </div>
        <div class="numberText"></div>
      </div>
      <div class="formItem">
        <div class="label"></div>
        <div class="input">
          <el-upload
            class="uploadFile"
            list-type="picture"
            action="https://jsonplaceholder.typicode.com/posts/9"
            :auto-upload="false"
            :on-remove="handleRemove"
            :on-exceed="handleExceed"
            :on-change="saveToUrl"
            :limit="1"
            :file-list="fileList">
            <el-button size="small" class="CoResetBtn" icon="el-icon-upload2"></el-button>
            <div slot="tip" class="el-upload__tip">.png .jpg... 48px*48px</div>
          </el-upload>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()"></el-button>
        <el-button class="CoResetBtn" @click="goPrePage()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import TASK from 'admin/service/task-service.js'
  import urlToFile from '@/utils/urlToFile.js'
  export default {
    name: "Watch",
    data(){
      return{
        fileList:[],
        data:{},
        fileArray:[]
      }
    },
    created(){
      this.data = this.$route.query;
      console.log('',this.data)
      let imgUrl = this.data.imgUrl;
      if(imgUrl && imgUrl != ''){
        let imgFileItem = [{
          'url' : this.data.imgUrl
        }];
        this.fileList = imgFileItem;
        urlToFile.imgUrlToFile(imgUrl,
          '').then(fileObj => {
          console.log('111', fileObj); // file
          this.fileArray.push(fileObj);
        });
      }
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
        this.fileArray = [];
      },
      saveToUrl(event,fileList){
        this.fileArray.push(event.raw)
      },
      handleExceed(files, fileList) {
        this.$message.warning('');
      },
      //
      saveForm() {
        this.$confirm('?', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        }).then(() => {
          let formData = new FormData();

          formData.append('fileArray', this.fileArray[0]);
          formData.append('reward', this.data.reward);
          formData.append('uuid', this.data.uuid);
          formData.append('requireAmount', this.data.requireAmount);

          TASK.updateMission(formData)
            .then(res => {
              console.log('res',res);
              if(res.code === "0"){
                this.$message({
                  type: 'success',
                  message: ''
                });
                this.$router.go(-1);
              }
            })

        }).catch(() => {
          console.log('');
        });

      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }

</style>



