          </div>
          <div class="numberText"></div>
        </div>
        <div class="formBtnGroup">
          <el-button type="primary" class="CoBtn" @click="saveForm()"></el-button>
          <el-button class="CoResetBtn" @click="goPrePage()"></el-button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "AddPoster",
      data(){
        return{
          name : '',
          status : '',
          num : '',
          selectOptions: [
            {
              value: '1',
              label: ''
            }, {
              value: '2',
              label: ''
            }
          ],
          checkList:[]
        }
      }
    }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



